.products {
  padding-top: 60px;
  padding-bottom: 60px;
  text-align: center;
}

.products__label {
  display: block;
  margin-bottom: 12px;

  font-weight: 800;
  font-size: 18px;
  line-height: calc(22 / 18);
  text-transform: uppercase;

  color: var(--text-color);

  @media screen and (min-width: 768px) {
    font-size: 22px;
    line-height: calc(27 / 22);
  }
  @media screen and (min-width: 1200px) {
    font-size: 30px;
    line-height: calc(37 / 30);
  }
}

.products__title {
  display: block;
  margin-bottom: 148px;

  font-family: 'Seymour One', sans-serif;
  font-size: 30px;
  line-height: calc(38 / 30);

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--text-color);
  @media screen and (min-width: 768px) {
    margin-bottom: 200px;
    font-size: 34px;
    line-height: calc(43 / 34);
  }
  @media screen and (min-width: 1200px) {
    margin-bottom: 303px;
    font-size: 36px;
    line-height: calc(45 / 36);
    letter-spacing: 0.06em;
  }
}
.products-list {
  display: flex;
  flex-direction: column;
  gap: 168px;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    gap: 16px;
  }
  @media screen and (min-width: 1200px) {
    gap: 29px;
  }
}
.products-tumba {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  position: relative;
  padding: 123px 40px 16px;
  min-height: 420px;
  border-radius: 24px;

  &::before {
    content: '';
    position: absolute;
    top: -100px;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    height: 200px;
    border-radius: 50%;
    @media screen and (min-width: 1200px) {
      width: 302px;
      height: 302px;
      top: -151px;
    }
  }
  @media screen and (min-width: 768px) {
    width: calc((100% - 16px) / 3);
    padding: 108px 26px 12px;
  }
  @media screen and (min-width: 1200px) {
    gap: 29px;
    width: calc((100% - 29px) / 3);
    padding: 165px 64px 60px;
  }
}
.products-list__item--green {
  background-color: #aec6a5;
  &::before {
    content: '';
    background: linear-gradient(
        0deg,
        rgba(156, 190, 143, 0.7),
        rgba(156, 190, 143, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.34219px 20.0332px rgba(10, 136, 23, 0.25);
  }
  &::after {
    content: '';
    position: absolute;
    top: -111px;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    height: 200px;

    background-image: url(../images/ice-cream.png);
    background-repeat: no-repeat;
    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(../images/ice-cream-x2.png);
      background-size: contain;
    }
    @media screen and (min-width: 1200px) {
      width: 302px;
      height: 302px;
      top: -167px;
      background-image: url(../images/ice-cream-pc.png);
      @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
        background-image: url(../images/ice-cream-pc-x2.png);
        background-size: contain;
      }
    }
  }
}
