.buy-modal__title {
  @include tablet {
    margin-bottom: 40px;
  }
}

.buy-modal__product-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-bottom: 31px;

  @include tablet {
    flex-direction: row;
    gap: 32px;
    margin-bottom: 40px;
  }
}

.buy-modal__product-item {
  width: 132px;
  height: 200px;
  display: flex;
  align-items: flex-end;
}

.buy-modal__product-description {
  width: 132px;
  height: 136px;
  position: relative;

  display: block;
  cursor: pointer;

  filter: drop-shadow(0px 5.26027px 19.726px rgba(212, 20, 67, 0.1));
  border-radius: 15px;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;

  text-align: center;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: var(--text-color);
  padding-top: 96px;
  padding-bottom: 20px;

  &::before {
    content: '';
    position: absolute;
    bottom: 60px;
    left: 50%;
    transform: translateX(-50%);
    width: 132px;
    height: 132px;
    border-radius: 50%;
  }

  &::after {
    content: '';
    position: absolute;
    bottom: 67px;
    left: 50%;
    transform: translateX(-50%);
    width: 132px;
    height: 132px;
    border-radius: 50%;
  }
}

.buy-modal__ice-cream {
  border: 2px solid #96af8c;
  .buy-now-check:checked + & {
    background-color: #96af8c;
  }

  &::before {
    background: linear-gradient(
        0deg,
        rgba(156, 190, 143, 0.7),
        rgba(156, 190, 143, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.26027px 19.726px rgba(10, 136, 23, 0.25);
  }

  &::after {
    background-image: url('../images/images-modal/ice-cream-pic-modal-1x.jpg');

    @include retina {
      background-image: url('../images/images-modal/ice-cream-pic-modal-2x.jpg');
      background-size: 100%;
    }
  }
}

.buy-modal__ice-coffee {
  border: 2px solid #efd478;
  .buy-now-check:checked + & {
    background-color: #efd478;
  }

  &::before {
    background: linear-gradient(
        0deg,
        rgba(248, 210, 78, 0.7),
        rgba(248, 210, 78, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.26027px 19.726px rgba(153, 114, 23, 0.25);
  }

  &::after {
    background-image: url('../images/images-modal/ice-coffee-pic-modal-1x.jpg');

    @include retina {
      background-image: url('../images/images-modal/ice-coffee-pic-modal-2x.jpg');
      background-size: 100%;
    }
  }
}

.buy-modal__milkshake {
  border: 2px solid #d6936d;
  .buy-now-check:checked + & {
    background-color: #d6936d;
  }

  &::before {
    background: linear-gradient(
        0deg,
        rgba(213, 117, 63, 0.7),
        rgba(213, 117, 63, 0.7)
      ),
      #d9d9d9;
    box-shadow: 0px 5.26027px 19.726px rgba(0, 0, 0, 0.25);
  }

  &::after {
    background-image: url('../images/images-modal/milkshake-pic-modal-1x.jpg');

    @include retina {
      background-image: url('../images/images-modal/milkshake-pic-modal-2x.jpg');
      background-size: 100%;
    }
  }
}

